.center {
  text-align: center;
}

.name {
  font-size: 30px;
  font-style: italic;
}

.points {
  font-size: 100px;
}

.green {
  color: rgb(36, 190, 36);
}

.red {
  color: rgb(226, 40, 40);
}

li {
  list-style-type: none;
}

#pointsCard {
  background-color: rgba(33, 37, 41, 0.25) !important;
  border-radius: 15px;
}

#myNav {
  background-color: rgba(33, 37, 41, 0.5) !important;
}

hr.styledHrGreen,
hr.styledHrRed {
  margin-top: 4px;
  border: 0;
  height: 1px;
  background-image: linear-gradient(
    to right,
    rgba(33, 37, 41, 1),
    rgba(0, 0, 0, 0),
    rgba(33, 37, 41, 1)
  );
}

@media screen and (max-width: 700px) {
  .name {
    margin-top: 20%;
  }
}

@media screen and (min-width: 700px) {
  .name {
    margin-top: 5%;
  }
}
